#bird {
  width: 3em;
  height: 3em;
  position: absolute;
  top: 250px;
  left: 40px;
  margin-bottom: 20px;
}

@font-face {
  font-family: 'Hooge';
  src: local('Hooge'), url(/bird/static/media/hooge_05_55_Cyr2.d79b9614.ttf) format('truetype');
}

body {
  background-color: #ffffff;
  font-family: 'Noto Sans', sans-serif;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}
#score {
  font-size: 3.5em;
  color: #f44336;
  position: relative;
  font-weight: bold;
  text-align: center;
  z-index: 15;
}
#gameOver {
  font-size: 3em;
  color: #03A9F4;
  position: relative;
  font-weight: bold;
  top: 150px;
  text-align: center;
  z-index: 15;
  width: 30%;
  height: 170px;
  margin: 0 auto;
  background-color: #424242;
}

#gameOver div {
  padding-top: 2%;
}

#gameOver button {
  background-color: #009688;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  margin-bottom: 2%;
}

.gameOver_point-container {
  border: 1px solid #003399;
  width: 80%;
  padding: 20px 0;
  margin: 30px auto 20px;
  border-radius: 10px;
  text-align: center;
}

.gameOver_point-container h2 {
  margin: 5px 0;
  color: #003399;
}

.gameOver_text {
  text-align: center;
  margin-bottom: 20px;
}

.gameOver_button {
  margin-bottom: 20px;
}

.gameOver_imageWrapper {
  text-align: center;
}

.gameOver_image {
  width: 100px;
  margin-bottom: 20px;
}
#startScreen {
  color: #03A9F4;
  font-size: 2em;
  font-weight: bold;
  width: 100%;
  height: 100%;
  display: table;
  position: absolute;
  text-align: center;
}

#startScreen div {
  display: table-cell;
  vertical-align: middle;
}

.startScreen_mb {
  margin-bottom: 15px;
}

.startScreen_imageWrapper {
  text-align: center;
  margin-bottom: 20px;
}

.startScreen_image {
  width: 150px;
}

.startScreen_paragraph {
  text-align: center;
  font-size: 13px;
}
