/*
    Information containet
*/

.info-container {
    position: relative;
    padding: 30px 20px;
    background-color: #fff;
    width: 70%;
    margin: 0 auto;
}

.info-container::before {
    content: '';
    position: absolute;
    width: 95%;
    height: 10px;
    display: block;
    top: -10px;
    left: 50%;
    background: #fff;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
}

.info-container::after {
    content: '';
    position: absolute;
    width: 95%;
    height: 10px;
    display: block;
    bottom: -10px;
    left: 50%;
    background: #fff;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
}

