#gameOver {
  font-size: 3em;
  color: #03A9F4;
  position: relative;
  font-weight: bold;
  top: 150px;
  text-align: center;
  z-index: 15;
  width: 30%;
  height: 170px;
  margin: 0 auto;
  background-color: #424242;
}

#gameOver div {
  padding-top: 2%;
}

#gameOver button {
  background-color: #009688;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  margin-bottom: 2%;
}

.gameOver_point-container {
  border: 1px solid #003399;
  width: 80%;
  padding: 20px 0;
  margin: 30px auto 20px;
  border-radius: 10px;
  text-align: center;
}

.gameOver_point-container h2 {
  margin: 5px 0;
  color: #003399;
}

.gameOver_text {
  text-align: center;
  margin-bottom: 20px;
}

.gameOver_button {
  margin-bottom: 20px;
}

.gameOver_imageWrapper {
  text-align: center;
}

.gameOver_image {
  width: 100px;
  margin-bottom: 20px;
}