.ticket_container {
    border: 1px solid #003399;
    width: 80%;
    padding: 20px 0;
    margin: 30px auto 100px;
    border-radius: 10px; 
}

.ticket_paragraph {
    text-align: center;
    text-align: center;
    font-size: 13px;
    line-height: 1.3;
}

.ticket_title {
    color: #003399;
}

.ticket_myTicket_title {
    text-align: center;
    font-family: 'Hooge';
    color: #003399;
    font-size: 30px;
    margin: 20px 0;
}

.ticket_myTicket_list {
    padding: 0;
    list-style-type: none;
    margin: 40px 0;
}

.ticket_myTicket_list li {
    padding: 10px 0;
    border-bottom: 2px solid #003399;
}

.ticket_myTicket_list li:first-child {
    border-top: 2px solid #003399;
}