.button {
    color: #ffffff;
    text-decoration: none;
    background-size: contain;
    width: 200px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Hooge';
}

.button_point-wrapper {
    color: #ffffff;
    border: none;
    width: 200px;
    height: 40px;
    background-size: contain;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: 'Hooge';

}

.button_point {
    border: none;
    color: #ffffff;
    width: 65%;
    background-color: transparent;
    font-family: 'Hooge';

}

.button_point-point {
    color: #003399;
    margin-right: 20px;
}